@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
	:root {
		/* --font-sans: here if you have one */
		/* --font-mono: here if you got it... */

		/* prefixed with foreground because it should look good on the background */
		--foreground-destructive: 345 82.7% 40.8%;

		--background: 0 0% 100%;
		--foreground: 222.2 84% 4.9%;

		--muted: 210 40% 93%;
		--muted-foreground: 215.4 16.3% 30%;

		--popover: 0 0% 100%;
		--popover-foreground: 222.2 84% 4.9%;

		--card: 0 0% 100%;
		--card-foreground: 222.2 84% 4.9%;

		--border: 214.3 31.8% 91.4%;
		--input: 214.3 31.8% 91.4%;
		--input-invalid: 0 84.2% 60.2%;

		--primary: 222.2 47.4% 11.2%;
		--primary-foreground: 210 40% 98%;

		--secondary: 210 20% 83%;
		--secondary-foreground: 222.2 47.4% 11.2%;

		--accent: 210 40% 90%;
		--accent-foreground: 222.2 47.4% 11.2%;

		--destructive: 0 70% 50%;
		--destructive-foreground: 210 40% 98%;

		--ring: 215 20.2% 65.1%;

		--radius: 0.5rem;
	}

	.dark {
		--background: 222.2 84% 4.9%;
		--foreground: 210 40% 98%;

		/* prefixed with foreground because it should look good on the background */
		--foreground-destructive: -4 84% 60%;

		--muted: 217.2 32.6% 12%;
		--muted-foreground: 215 20.2% 65.1%;

		--popover: 222.2 84% 4.9%;
		--popover-foreground: 210 40% 98%;

		--card: 222.2 84% 4.9%;
		--card-foreground: 210 40% 98%;

		--border: 217.2 32.6% 17.5%;
		--input: 217.2 32.6% 17.5%;
		--input-invalid: 0 62.8% 30.6%;

		--primary: 210 40% 98%;
		--primary-foreground: 222.2 47.4% 11.2%;

		--secondary: 217.2 20% 24%;
		--secondary-foreground: 210 40% 98%;

		--accent: 217.2 32.6% 10%;
		--accent-foreground: 210 40% 98%;

		--destructive: 0 60% 40%;
		--destructive-foreground: 0 85.7% 97.3%;

		--ring: 217.2 32.6% 60%;
	}
    
	.fixed-table-wrap {
		display: block;
		width: 100vw;
		overflow-x: auto;
		overflow-y: auto;
		height: 70vh;
		-ms-overflow-style: -ms-autohiding-scrollbar;
	}
	
	.fixed-table-wrap table th,
	.fixed-table-wrap table td {
		padding: 2px;
	}
	
	.fixed-table-wrap table th {
		position: sticky;
		position: -webkit-sticky;
		top: 0px;
		z-index: 99;
	}
	
	.fixed-table-wrap table th::after {
		content: "";
		display: inline-block;
		width: 1px;
		position: absolute;
		right: -1px;
		height: 40px;
		top: 0px;
	}
	
	.text-align-webkit-center {	text-align: -webkit-center;}
	.bg-muted-40 { background-color: hsl(var(--muted) / 0.4);}

	.light-filter {
		filter: invert(100%) sepia(100%) saturate(2%) hue-rotate(261deg) brightness(106%) contrast(100%);
	}

	.border-1 { border-width: 1px; }

	.justify-content-right { justify-content: right; }
	
	.grid-cols-20 {
		grid-template-columns: repeat(20, minmax(0, 1fr));
	}
	.table-fixed thead th {
    position: sticky;
    position: -webkit-sticky;
    top: 0;
		border-spacing: 10px 0px;
	}

}
